import React, { useCallback, useState } from 'react'
import { useLocation, navigate } from '@reach/router'
import {
  Card,
  CardContent,
  TextField,
  IconButton,
  Input,
  InputAdornment,
  Button,
  Typography
} from '@material-ui/core'
import { Formik, Form, Field, ErrorMessage } from 'formik'

import * as Yup from 'yup'

import Visibility from '@material-ui/icons/Visibility'
import VisibilityOff from '@material-ui/icons/VisibilityOff'
import getLoginUrl from 'src/utils/getLoginUrl'
import Facebook from 'src/components/SocialIcon/Facebook'
import http from 'src/utils/http'
import useLoginFunction from 'src/hooks/useLoginFunction'

export default function Register() {
  const localLogin = useLoginFunction('local')

  const [showPassword, setshowPassword] = useState(false)
  const togglePassword = () => setshowPassword(val => !val)

  return (
    <Card style={{ margin: '2em auto', maxWidth: '400px' }}>
      <CardContent>
        <Formik
          initialValues={{
            username: '',
            password: '',
            passwordConfirm: '',
            email: ''
          }}
          onSubmit={values => {
            http
              .post(`/auth/register`, {
                name: values.username,
                password: values.password,
                email: values.email
              })
              .then(() =>
                localLogin({
                  username: values.username,
                  password: values.password
                })
              )
          }}
          validationSchema={Yup.object().shape({
            username: Yup.string()
              .required('')
              .min(6, '6'),
            password: Yup.string()
              .required('')
              .min(6, '6'),
            passwordConfirm: Yup.string()
              .required('')
              .min(6, '6'),

            email: Yup.string()
              .email('')
              .required('')
          })}
        >
          {props => {
            const {
              values,
              handleChange,
              handleBlur,
              handleSubmit,
              handleReset,
              errors,
              touched
            } = props

            return (
              <Form onSubmit={handleSubmit}>
                <TextField
                  label=""
                  name="username"
                  fullWidth
                  variant="filled"
                  value={values.username}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  error={errors.username}
                  helperText={
                    errors.username && touched.username && errors.username
                  }
                  margin="normal"
                />

                <TextField
                  label=""
                  name="email"
                  fullWidth
                  variant="filled"
                  value={values.email}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  error={errors.email}
                  helperText={errors.email && touched.email && errors.email}
                  margin="normal"
                ></TextField>
                <TextField
                  label=""
                  name="password"
                  type={showPassword ? 'text' : 'password'}
                  fullWidth
                  variant="filled"
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  error={errors.password}
                  helperText={
                    errors.password && touched.password && errors.password
                  }
                  margin="normal"
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={togglePassword}
                          // onMouseDown={togglePassword}
                        >
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                />

                <TextField
                  label=""
                  name="passwordConfirm"
                  type={showPassword ? 'text' : 'password'}
                  fullWidth
                  variant="filled"
                  value={values.passwordConfirm}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  error={errors.passwordConfirm}
                  helperText={
                    errors.passwordConfirm &&
                    touched.passwordConfirm &&
                    errors.passwordConfirm
                  }
                  margin="normal"
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={togglePassword}
                          // onMouseDown={togglePassword}
                        >
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                ></TextField>

                <div style={{ textAlign: 'center' }}>
                  <Button size="large" type="submit">
                    
                  </Button>
                </div>
              </Form>
            )
          }}
        </Formik>
      </CardContent>
    </Card>
  )
}
